'use strict'

const CacheControl = (function() {
  
  // let cachedContent = {
  //   '/data': this.get.bind(this),
  //   '/route': this.get.bind(this)
  // }

  const staticCacheName = 'transport-static-v2'

  const _object = {

    getCacheName: function() {
      return staticCacheName
    },
    putIntoCache: function(event, response) {
      caches.open(staticCacheName).then((cache) => {
        // console.log('Putting response in in Cache')
        cache.put(event.request, response)
      }).catch(err => {
        // console.error('Putting into Cache failed:', err)
        throw err
      })
    },
    getFromCache: function(event, callback) {

      // console.log('GETTING FROM CACHE')
      // console.log(event.request)
      // console.log(event)
      // console.log(event.url)
      // console.log(event.request.url.indexOf('/data/'))
      // console.log(event.request.url.indexOf('/routes/'))

      if (event.url) {
        if( event.url.indexOf('/data/') !== -1 ) {
          console.log('FOUND DATA IN REQUEST')
          event.request = new Request('data/')
        } else if (event.url.indexOf('/routes/') !== -1 ) {
          console.log('FOUND ROUTES IN REQUEST')
          event.request = new Request('routes/')
        }
      }

      return caches.match(event.request).then((response) => {
        if (response && response.clone().ok) {
          // console.log('Found response in cache:', response.clone())
          return response
        }
        // console.log('No response found in cache. About to fetch from network...')
        // if (cache === false)
        //   return

        // return new Response(['resp'])
        return callback()
          .then(resp => {
            // console.log(response.clone().json())
            // if (!cache)
            //   return
            // if (event.url) {
            //   let init = { 'status': 200, 'statusText': "OK"}
            //   let response = new Response(resp, init)
            //   CacheControl.putIntoCache(event, response.clone())
            //   return response
            // } else {
            //   CacheControl.putIntoCache(event, resp.clone())
            //   return resp
            // }
            
            let init = { 'status': 200, 'statusText': "OK"}
            let response = new Response(resp, init)
            CacheControl.putIntoCache(event, response.clone())
            return response
            
            // console.log(response.json())

            // let clone = response.clone()
            // console.log(clone.json())
            
          })
          .catch(err => console.log(err))

        // return fetch(event.request).then((res) => {
        //   console.log(res)
        //   if ( res ) {
        //     console.log(res.clone())
        //     this.putIntoCache( event, res.clone(), this.cacheName)

        //     console.log('here is response', res)
        //     return res
        //   } else {

        //     // response is not OK at all
        //     // return new custom bullshit response

        //   }          

        //   // return res
        // }).catch((error) => {
        //   console.error('Fetching failed:', error)
        //   throw error
        // })
      })

      
    }
  }

  return _object

})()

export default CacheControl

